.header {
  position: absolute;
  content: "";
  display: block;
  width: 100%;

  & nav {
    display: flex;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 31px;
  padding-right: 15px;
  padding-bottom: 18px;
  padding-left: 15px;

  @media (max-width: $breakpoint-tablet) {
    padding-top: 21px;
    padding-right: 25px;
    padding-bottom: 23px;
    padding-left: 25px;
  }

  @media (max-width: $breakpoint-mobile) {
    padding-top: 17px;
    padding-right: 15px;
    padding-bottom: 14px;
    padding-left: 15px;
  }
}

.header__button {
  @include header-button;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $breakpoint-tablet) {
    display: none;
  }
}

.header__logo {
  display: flex;

  & img {
    width: 202px;
    height: 52px;

    @media (max-width: $breakpoint-mobile) {
      width: 38px;
      height: 38px;
    }
  }
}

.header__contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 430px;

  @media (max-width: $breakpoint-tablet) {
    margin-top: -6px;
    width: 84px;
  }

  @media (max-width: $breakpoint-mobile) {
    margin-top: -3px;
    width: 74px;
  }

  & span {
    font-family: "PT Mono", "Courier New", monospace;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    color: $white;
    margin-left: 37px;

    @media (max-width: $breakpoint-tablet) {
      display: none;
    }
  }

  & a {
    margin-top: 11px;
    position: relative;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.5;
    }

    @media (max-width: $breakpoint-tablet) {
      margin-top: -5px;
      width: 26px;
      height: 26px;

      &:first-child {
        margin-top: -3px;
        width: 28px;
        height: 21px;
      }
    }
  }

  & a:first-child svg {
    position: absolute;
    top: 3px;
    width: 19px;
    height: 14px;

    @media (max-width: $breakpoint-tablet) {
      width: 26px;
      height: 19px;
    }
  }

  & a:last-child svg {
    position: absolute;
    content: "";
    top: 2px;
    left: 2px;
    width: 18px;
    height: 18px;

    @media (max-width: $breakpoint-tablet) {
      width: 24px;
      height: 24px;
    }
  }
}