@mixin no-outline {
  outline: none;
  background: transparent;

  &::before,
  &::after {
    display: none;
  }
}

@mixin header-button {
  display: block;
  padding-top: 13px;
  padding-right: 26px;
  padding-bottom: 14px;
  padding-left: 27px;
  margin-left: 50px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  background-color: transparent;
  border: 1px solid $white;
  border-radius: 5px;
  cursor: pointer;
}